import EventEmitter from 'events';

class Loader {
	constructor(config) {
		this.TAG = 'Loader';

		this._config = config;

		this._pump();
	}

	_pump() {
		let data = {
			chunk: 1,
			byteStart: 0,
			receivedLength: 1
		};

		setInterval(() => {
			console.log(this);

			this.onDataArrival(data);
		}, 3000);
	}
}

export default Loader;
